@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
*{
  margin: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  /* overflow-y: scroll; */
  /* display: flex; */
  /* flex-direction: column; */
  font-family: 'Inter', sans-serif;
  /* border: 10px solid blue; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
	background-color: white;
	height: 100vh;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	overflow: auto;
}

.app-dark{
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	overflow: auto;
	background-color: hsl(230, 17%, 14%);
}

.appBackground{
	background-color: hsl(225, 100%, 98%);
	height: 30vh;
	width: 100%;
}

.appBackgroundRem{
	background-color: white;
	width: 100%;
	height: 70vh;
	position: absolute;
	bottom: 0;
}


.appBackground-dark{
	background-color: hsl(232, 19%, 15%);
	height: 210px;
	width: 100%;
}

.pseudoBody{
	position:absolute;
	top: 0;
	min-height: 100vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	z-index: 999;
}

.container{
	height: 100vh;
	width: 80%;
	flex: 0.9 1;
	display: flex;
	flex-direction: column;
	padding-block: 20px;
	justify-content: space-around;
}

.header{
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-bottom: 30px;
}


.socialMediaHeader{
	color: hsl(230, 17%, 14%);
}

.socialMediaHeader-dark{
	color: hsl(0, 0%, 100%);
}

.totalFollowersText{font-size: 14px; font-weight: 400; color:hsl(228, 12%, 44%)}

.themeToggler{
	display: flex; 
	align-items: center; 
	justify-content: center; 
	flex-wrap: wrap;
	font-weight: 700; color:hsl(228, 12%, 44%)}

.mainCards{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	grid-gap: 20px;
	gap: 20px;  	
}

.overview{
	display: flex;
	flex-direction: column;
	padding-top: 10px;
	justify-content: space-evenly;
	margin-top: 25px;
	flex: 1 1;
}

.overviewHeading{
	margin: 0;
	color:hsl(228, 12%, 44%);
}

.overviewHeading-dark{
	margin: 0;
	color:white;
}





.overviewCards{
	display: flex;
	justify-content: space-between;	
	flex-wrap: wrap;
	grid-gap: 30px;
	gap: 30px;
}


.mainCardContainer{
    width: 250px;
    height: 180px;
    border-radius: 5px;
}

.instagram{
    background-image: linear-gradient(to right, hsl(37, 97%, 70%), hsl(329, 70%, 58%));
}

.facebook{
    background-color: hsl(208, 92%, 53%);
}

.twitter{
    background-color: hsl(203, 89%, 53%);
}

.youtube{
    background-color: hsl(348, 97%, 39%);
}

.mainCard{
    width: 100%;
    height: 100%;
    background-color: hsl(227, 47%, 96%);
    margin-top: 3px;
    border-radius: 0 0 5px 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.mainCard-dark{
    width: 100%;
    height: 100%;
    background-color: hsl(228, 28%, 20%);
    margin-top: 3px;
    border-radius: 0 0 5px 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.mainCardStats{
    font-size: 50px; 
    font-weight: 700; 
    color: hsl(230, 17%, 14%)
}

.mainCardStats-dark{
    font-size: 50px; 
    font-weight: 700; 
    color: hsl(0, 0%, 100%)
}

.iconHandle{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px;
    padding-top: 10px;
}

.iconHandle > img{
    object-fit: contain;
}

.iconHandle > p, .statistics > p{
    color: hsl(228, 12%, 44%);
}

.statistics{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.trend{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 700;
    grid-gap: 5px;
    gap: 5px;
}

.positive{
    color: hsl(163, 72%, 41%)
}

.negative{
    color: hsl(356, 69%, 56%)
}
.overviewCardContainer{
    height: 107px;
    width: 252px;
    border-radius: 2px;
    background-color: hsl(228, 34%, 66%);
}

.overviewCard{
    width: 100%;
    height: 100%;
    background-color: hsl(227, 47%, 96%);
    border-radius: 10px;
    /* border-bottom-right-radius: 0px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding-inline: 10px;
}

.overviewCard-dark{
    height: 107px;
    width: 252px;
    background-color: hsl(228, 28%, 20%);
    border-radius: 10px;
    /* border-bottom-right-radius: 0px; */

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding-inline: 10px;
}

.statNameIcon, .statTrend{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.statNameIcon > p{
    color: hsl(228, 34%, 66%);
}
